	module.exports = {"pathContext":{"docHtml":"<h1>Roadmap</h1>\n<ul>\n<li>Make it easier to use and contribute to the project.</li>\n<li>Install Argo on any existing k8s cluster.</li>\n<li>Integrate Argo with k8s RBAC &#x26; secrets.</li>\n<li>Support for running (KinK) Kubernetes in Kubernetes.</li>\n</ul>\n<h1>History</h1>\n<ul>\n<li>\n<p>M1: Nov 2015</p>\n<ul>\n<li>Complete \"hardwired\" CI/CD workflow for a simple web application.</li>\n<li>Using Mesos and rabbitmq/celery for the workflow engine.</li>\n</ul>\n</li>\n<li>\n<p>M2: Jan 2016</p>\n<ul>\n<li>Persistent volume support using flocker.</li>\n<li>Initial \"Cashboard\" implementation.</li>\n<li>Automated installer for AWS.</li>\n</ul>\n</li>\n<li>\n<p>M3: May 2016</p>\n<ul>\n<li>GUI.</li>\n<li>GUI-based DSL.</li>\n<li>Artifacts for workflows.</li>\n<li>Container log management.</li>\n<li>Cluster autoscaling.</li>\n<li>Many, many volume management bugs.</li>\n</ul>\n</li>\n<li>\n<p>M4: Jul 2016</p>\n<ul>\n<li>Nested workflows.</li>\n<li>Time-based job scheduling.</li>\n</ul>\n</li>\n<li>\n<p>M5: Oct 2016</p>\n<ul>\n<li>Switched to K8s.</li>\n<li>Spot instances.</li>\n<li>Fixtures.</li>\n<li>Improve artifacts.</li>\n<li>YAML DSL.</li>\n<li>Email notificaiton.</li>\n<li>Non-disruptive upgrades of platform software.</li>\n<li>Make flocker really work on AWS</li>\n</ul>\n</li>\n<li>\n<p>M6: Dec 2016</p>\n<ul>\n<li>Scale AXDB.</li>\n<li>Scale internal event handling</li>\n<li>Performance</li>\n<li>Run chaos monkey</li>\n<li>Hardening.</li>\n</ul>\n</li>\n<li>\n<p>M7: Mar 2017</p>\n<ul>\n<li>AppStore.</li>\n<li>Spot instances.</li>\n<li>Artifact management.</li>\n<li>Deployment.</li>\n<li>Improved artifact management.</li>\n<li>Improve non-disruptive upgrade.</li>\n</ul>\n</li>\n<li>\n<p>M8: May 2017</p>\n<ul>\n<li>Persistent volumes.</li>\n<li>Notification center.</li>\n<li>Secret management.</li>\n</ul>\n</li>\n<li>\n<p>M9: Jun 2017</p>\n<ul>\n<li>Rolling upgrade of deployments.</li>\n<li>Secret management v2.</li>\n<li>Remove rabbitmq.</li>\n<li>Managed ELBs.</li>\n<li>Prometheus.</li>\n<li>Managed fixtures (RDS, VM).</li>\n<li>Initial GCP/GKE support.</li>\n</ul>\n</li>\n<li>\n<p>M10: Aug 2017</p>\n<ul>\n<li>Ready to release to the world!</li>\n<li>Remove rabbitmq.</li>\n<li>YAML checker v2</li>\n<li>Kubernetes 1.6.</li>\n<li>Dev CLI tool.</li>\n<li>Move to kops</li>\n</ul>\n</li>\n<li>\n<p>M11: Nov 2017</p>\n<ul>\n<li>Kubernetes-in-Kubernetes (KinK)</li>\n<li>Support for minikube and GKE</li>\n<li>Argolite proof of concept (conceptual basis for Argo 2.0)</li>\n</ul>\n</li>\n<li>\n<p>M12: Dec 2017</p>\n<ul>\n<li>Argo 2.0!</li>\n<li>Argo is now a Kubernetes CRD!</li>\n<li>Basic UI</li>\n<li>Examples</li>\n</ul>\n</li>\n<li>\n<p>M13: In progress</p>\n<ul>\n<li>Event triggers</li>\n<li>More examples</li>\n</ul>\n</li>\n</ul>","docPath":"argo/roadmap","proj":"argo"}}

